












import Vue from 'vue'
import { ExpLinkableImage } from '@/common/models/ExpLinkableImage'

export default Vue.extend({
  name: 'ExpLinkableImage',
  props: {
    model: {
      type: ExpLinkableImage.Model,
      required: true
    }
  },
  computed: {
    hasLink (): boolean {
      return this.model.link != null
    }
  }
})
